// AdminForm.JS
import React, { useEffect, useState } from "react";
import { Button, Form, Container, Row, Col } from "react-bootstrap";
import { useNavigate, useParams } from "react-router-dom";

import "../styles/admin.css";

import fetchUserDataById from "./api/userDataApi";
import TopicWithPercent from "./formFunction.js/addRemove";

const AdminForm = (props) => {
  // const [data, setData] = useState(null);
  // const [fetchedData, setfetchedData] = useState(null)
  // const [error, setError] = useState(null);
  const [fileData, setFileData] = useState();
  const navigate = useNavigate();
  const { userId } = useParams();
  // const myparam = location.state.userId;
  const [formData, setFormData] = useState({
    name: "",
    technologies: "",
    complexity: "",
    noOfQuestions: "",
    timeDuration: "",
    internetreqirement: "YES",
    uploadDocument: null,
    topics: [],
    percentages: [],
    passPercentage: "",
  });

  const [formErrors, setFormErrors] = useState({
    name: "",
    technologies: "",
    complexity: "",
    noOfQuestions: "",
  });
  useEffect(() => {
    if (userId) {
      fetchUserDataById(userId)
        .then((response) => {
          console.log("response", response);
          setFormData((prevFormData) => {
            console.log("prevFormData", prevFormData);
            const updatedFormData = {
              ...prevFormData,
              name: response[0].Name,
              technologies: response[0].Technologies,
              technologiesPercentage: response[0].TechnologiesPercentage,
              complexity: response[0].Complexity,
              noOfQuestions: response[0].NoOfQuestions,
              timeDuration: response[0].TimeDuration,
              internetreqirement: (response[0].internetrequirement = true
                ? "YES"
                : "NO"),
              // internetreqirement: response[0].Internetreqirement,
              UploadDocument: "null",
              passPercentage: response[0].PassPercentage,
            };
            console.log("formdata", updatedFormData);
            return updatedFormData;
          });
        })
        .catch((error) => {
          console.error("Error fetching user data by ID:", error);
        });
    }
  }, [userId]);

  const handleChange = (e) => {
    const { name, value, type, check, files } = e.target;
    if (type === "checkbox") {
      console.log(type);
    } else if (type === "file") {
      const file = e.target.files[0];
      

      var file_data = {
        name: file.name,
        size: file.size,
        type: file.type,
      };
      const reader = new FileReader();

      reader.onloadend = () => {
        // The result property contains the base64-encoded file content

        file_data["data"] = reader.result;
        //   setFileData(base64Data);
        console.log(file_data);
        setFileData(file);
        setFormData({ ...formData, [name]: JSON.stringify(file_data) });
      };

      // Read the file as a data URL (base64)
      reader.readAsDataURL(file);
    } else if (name === "technologies") {
      setFormData({ ...formData, [name]: value });
    } else {
      setFormData({ ...formData, [name]: value });
    }

    setFormErrors({ ...formErrors, [name]: "" });
  };
  const handleSubmit = async (e) => {
    e.preventDefault();

    const newErrors = {};

    // Validate name
    if (formData.name.trim() === "") {
      newErrors.name = "Please enter a name";
    }

    // Validate complexity
    if (!formData.complexity) {
      newErrors.complexity = "Please select a complexity level";
    }

    // Validate noOfQuestions
    if (!formData.noOfQuestions) {
      newErrors.noOfQuestions = "Please select the number of questions";
    }

    // Validate timeDuration (numeric and positive)
    const timeDuration = parseFloat(formData.timeDuration);
    if (isNaN(timeDuration) || timeDuration <= 0) {
      newErrors.timeDuration = "Please enter a valid time duration";
    }

    // Validate internetreqirement
    if (
      formData.internetreqirement !== "YES" &&
      formData.internetreqirement !== "NO"
    ) {
      newErrors.internetreqirement =
        "Please select an option for internet usage";
    }

    // Validate uploadDocument (if internetreqirement is 'NO')
    if (formData.internetreqirement === "NO" && !formData.uploadDocument) {
      newErrors.uploadDocument = "Please upload a document";
    }
    var internet;
    if (formData.internetreqirement === "YES") {
      internet = true;
    } else {
      internet = false;
    }

    if (Object.keys(newErrors).length === 0) {
      console.log("inside", formData);
      try {
        const payLoad = {
          Id: userId ? userId : "0",
          Name: formData.name,
          Technologies: formData.technologies,
          TechnologiesPercentage: formData.technologiesPercentage,
          Complexity: formData.complexity,
          NoOfQuestions: formData.noOfQuestions,
          TimeDuration: formData.timeDuration,
          Internetreqirement: internet,
          UploadDocument: internet ? "null" : formData.uploadDocument,
          PassPercentage: formData.passPercentage,
        };
        const response = await fetch(
          "https://quickapplication.azurewebsites.net/api/value/createorUpdateuser",
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify(payLoad),
          }
        );

        console.log("Post response", response.json());
      } catch (error) {
        console.error("Error posting data:", error.message);
      }
      navigate("/adminDashboard", { state: { formData } });
    }
    setFormErrors(newErrors);
  };

  const handleTopicWithPercentChange = (updatedData) => {
    // Update formData with the new data from TopicWithPercent
    setFormData((prevFormData) => ({
      ...prevFormData,
      technologiesPercentage: updatedData
        .map(({ topic, percentage }) => `${topic},${percentage}`)
        .join(","),
    }));
  };

  const handleBlob = async () => {
    // const file =fileData
    // console.log("fileeeeeeeeeeeeeeeeeeee",formData.uploadDocument)
    const formData = new FormData();
    formData.append('file', fileData);

    console.log(
      "formData.uploadDocumentformData.uploadDocumentformData.uploadDocument",
      fileData.data
    );
    try {
      const response = await fetch(
        "https://quickapplication.azurewebsites.net/api/value/uploadBlob",
        {
          method: "POST",
          body: formData,
        }
      );

      if (!response.ok) {
        throw new Error("Network response was not ok");
      }

      console.log("File uploaded successfully");
      // You can handle the response here
    } catch (error) {
      console.error("Error uploading file:", error.message);
      // You can handle errors here
    }
  };

  const handleradio = (e) => {
    const val = e.target.value;
    setFormData({ ...formData, internetreqirement: val });
    console.log("radio-formdata", formData);
  };
  return (
    <div className="bg-image">
      <Container>
        <Row>
          <Col lg={12} md={10} sm={12}>
            <Form onSubmit={handleSubmit} className="custom-form">
              <Form.Group controlId="name">
                <div className="form-contianer">
                  <Form.Label className="fill-name">
                    Examination Name
                  </Form.Label>
                  <Form.Control
                    type="text"
                    name="name"
                    placeholder="Examination Name"
                    value={formData?.name}
                    onChange={handleChange}
                  />
                  <span className="error" style={{ color: "Red" }}>
                    {formErrors.name}
                  </span>
                </div>
              </Form.Group>
              <Form.Group controlId="checkRequirement">
                <Form.Label className="fill-name">
                  Do you want to go with internet?
                </Form.Label>
                <div
                  style={{
                    display: "flex",
                    flexDirection: "row",
                    paddingLeft: "10px",
                  }}
                >
                  <Form.Check
                    type="radio"
                    id="yes-radio"
                    name="internetreqirement"
                    value="YES"
                    label="Yes"
                    check={formData.internetreqirement === "YES"}
                    onChange={handleradio}
                    defaultChecked
                  />
                  <Form.Check
                    type="radio"
                    id="no-radio"
                    name="internetreqirement"
                    value="NO"
                    label="No"
                    check={formData.internetreqirement === "NO"}
                    onChange={handleradio}
                    style={{ paddingLeft: "40px" }}
                  />
                </div>
                <span className="error" style={{ color: "Red" }}>
                  {formErrors.internetreqirement}
                </span>
              </Form.Group>
              {formData.internetreqirement === "NO" ? (
                <div style={{ display: "flex", flexDirection: "row" }}>
                  <Form.Group controlId="uploadDocument">
                    <Form.Label className="fill-name">
                      Upload Document
                    </Form.Label>
                    <Form.Control
                      type="file"
                      name="uploadDocument"
                      onChange={handleChange}
                    />
                    {/* <Button variant='info'>Add</Button> */}
                    <span className="error" style={{ color: "Red" }}>
                      {formErrors.uploadDocument}
                    </span>
                  </Form.Group>
                  <div style={{ marginTop: "30px" }}>
                    <Button
                      type="button"
                      variant="success"
                      onClick={handleBlob}
                    >
                      upload
                    </Button>
                  </div>
                </div>
              ) : (
                <>
                  <Form.Group controlId="technologies">
                    <div className="form-contianer">
                      <Form.Label className="fill-name">
                        Topic/Technology
                      </Form.Label>
                      <Form.Control
                        type="text"
                        name="technologies"
                        placeholder="Enter your technology"
                        value={formData?.technologies}
                        onChange={handleChange}
                      />
                      <span className="error" style={{ color: "Red" }}>
                        {formErrors.name}
                      </span>
                    </div>
                  </Form.Group>
                </>
              )}
              <TopicWithPercent
                formData={formData}
                setFormData={handleTopicWithPercentChange}
              />
              {/* <span style={{ color: 'red' }}>* sum of percentage should not be more them 100 </span> */}

              <Form.Group controlId="complexity">
                <Form.Label className="fill-name">Complexity</Form.Label>
                <Form.Select
                  name="complexity"
                  value={formData.complexity}
                  onChange={handleChange}
                >
                  <option value="" disabled>
                    Select
                  </option>
                  <option value="intermediate">intermediate</option>
                  <option value="medium">Medium</option>
                  <option value="high">High</option>
                </Form.Select>
                <span className="error" style={{ color: "Red" }}>
                  {formErrors.complexity}
                </span>
              </Form.Group>
              {/* <Form.Group controlId="noOfQuestions">
                                <Form.Label className='fill-name'>Number of Questions</Form.Label>
                                <Form.Control
                                    name="noOfQuestions"
                                    value={formData.noOfQuestions}
                                    type='text'
                                    onChange={handleChange}
                                   
                                />
                                <span className="error" style={{ color: 'Red' }}>{formErrors.noOfQuestions}</span>
                            </Form.Group> */}
              <Form.Group controlId="noOfQuestions">
                <Form.Label className="fill-name">
                  Number of Questions
                </Form.Label>
                <Form.Select
                  name="noOfQuestions"
                  value={formData.noOfQuestions}
                  onChange={handleChange}
                >
                  <option value="">Select</option>

                  <option value="10">10</option>
                  <option value="15">15</option>
                  <option value="20">20</option>
                  <option value="25">25</option>
                  {/* Add more options as needed */}
                </Form.Select>
                <span className="error" style={{ color: "red" }}>
                  {formErrors.noOfQuestions}
                </span>
              </Form.Group>

              <Form.Group controlId="timeDuration">
                <Form.Label className="fill-name">Time Duration</Form.Label>
                <Form.Control
                  type="text"
                  name="timeDuration"
                  placeholder="Time in Minutes"
                  value={formData?.timeDuration}
                  onChange={handleChange}
                />
                <span className="error" style={{ color: "Red" }}>
                  {formErrors.timeDuration}
                </span>
              </Form.Group>
              <Form.Group controlId="passPercentage">
                <Form.Label className="fill-name">Pass Percentage</Form.Label>
                <Form.Control
                  type="text"
                  name="passPercentage"
                  placeholder="Pass Percentage"
                  value={formData?.passPercentage}
                  onChange={handleChange}
                />
                <span className="error" style={{ color: "Red" }}>
                  {formErrors.timeDuration}
                </span>
              </Form.Group>
              <div
                style={{
                  display: "flex",
                  flexDirection: "row",
                  justifyContent: "space-around",
                }}
              >
                <Button variant="primary" type="submit" className="btn-submit">
                  Submit
                </Button>
                <Button
                  variant="success"
                  className="btn-submit"
                  onClick={() => {
                    navigate("/adminDashboard");
                  }}
                >
                  Admin Dashboard
                </Button>
              </div>
            </Form>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default AdminForm;
