import React, { useState, useEffect } from 'react';
import { Button, Spinner, Modal ,Card} from 'react-bootstrap';
import FinalSubmissionCard from './FinalSubmissionCard';
import ResultCard from './ResultCard';
import { json, useLocation, useNavigate, useParams } from 'react-router-dom';
import fetchUserDataById from '../api/userDataApi';
import '../../styles/QandA.css';

const QuestionData = () => {
  const { userId } = useParams();
  const navigate = useNavigate();
  const [questionsData, setQuestionsData] = useState([]);
  const [examDetails, setExamDetails] = useState(null);
  const [examName, setExamName] = useState(null);
  const [userAnswers, setUserAnswers] = useState(Array(questionsData.length).fill(null));
  const [selectedOptions, setSelectedOptions] = useState(Array(questionsData.length).fill(null));
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [showResult, setShowResult] = useState(false);
  const [showConfirmationModal, setShowConfirmationModal] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);

  const location=useLocation()
  const {examInfo,examQA}=location.state
  // console.log("examDataexamDataexamDataexamDataexamDataexamData",examQA)

  useEffect(() => {
    // const det = localStorage.getItem('ExamDetails');
    // console.log("examDataexamDataexamDataexamDataexamDataexamData",examInfo.ExamName)
    // if (det) {
      // setExamDetails(JSON.parse(det));
    // }
    // console.log("examQAexamQAexamQAexamQA",examQA)

    // let questionData = JSON.parse(localStorage.getItem('qt'));

    // if (!questionData || questionData.length === 0) {
    //   const intervalId = setInterval(() => {
    //     window.location.reload();
    //   }, 6000);
    //   questionData = [];
    // }

    setQuestionsData(examQA);

    // if (det) {
    //   const parsedExamDetails = JSON.parse(det);
    //   setExamName(parsedExamDetails.ExamName);
    // }

    console.log("userAnswersuserAnswersuserAnswers",userAnswers)
  }, []);

  const questionsPerPage = 10;
  const totalQuestions = questionsData.length;
  const totalPages = Math.ceil(totalQuestions / questionsPerPage);

  const handleAnswerClick = (questionIndex, selectedOption) => {
    const newSelectedOptions = [...selectedOptions];
    newSelectedOptions[questionIndex] = selectedOption;

    const newUserAnswers = [...userAnswers];
    newUserAnswers[questionIndex] = selectedOption;

    setSelectedOptions(newSelectedOptions);
    setUserAnswers(newUserAnswers);
  };

  const handleOpenModal = () => {
    setShowConfirmationModal(true);
  };

  const handleCloseModal = () => {
    setShowConfirmationModal(false);
  };

  const handleConfirmSubmit = () => {
    handleCloseModal();
    setIsSubmitted(true);
    navigate('/ResultCard/'+userId, {
      state: { userAnswers: { userAnswers }, questionsData: { questionsData },examInfo:{examInfo} },
    });
  };

  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
  };

  const startQuestionIndex = (currentPage - 1) * questionsPerPage;
  const endQuestionIndex = Math.min(currentPage * questionsPerPage, totalQuestions);

  return (
    <div className='MCQ-head'>
      {!isSubmitted ? (
        questionsData.length > 0 ? (
          <div className='questions'>
            <h1>  Exam: {examInfo.ExamName}</h1>
            {questionsData.map((question, index) => (
              <div key={index}>
                <h5 className='QA-h5'>{question.question_number}. {question.question_text}</h5>
                <form className='QA-form'>
                  {question.options.map((option, optionIndex) => (
                    <div className='QA-IL' key={optionIndex}>
                      <input
                        type="radio"
                        id={`${startQuestionIndex + index}-${optionIndex}`}
                        name={`answerOptions-${startQuestionIndex + index}`}
                        value={option}
                        onChange={() => handleAnswerClick(startQuestionIndex + index, option)}
                        checked={selectedOptions[startQuestionIndex + index] === option}
                      />
                      <label htmlFor={`${startQuestionIndex + index}-${optionIndex}`}>{option}</label>
                    </div>
                  ))}
                </form>
              </div>
            ))}
            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'end' }}>
              <Button onClick={handleOpenModal}>Submit</Button>
            </div>
          </div>
        ) : (
          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100vh' }}>
            <Spinner animation="border" role="status">
              <span className="sr-only"></span>
            </Spinner>
          </div>
        )
      ) : null}

      <Modal show={showConfirmationModal} onHide={handleCloseModal}>
        <Modal.Header closeButton>
          <Modal.Title>Confirmation</Modal.Title>
        </Modal.Header>
        <Modal.Body>Are you sure you want to submit the exam?
          <Card.Text>
            {/* <h3>Are you sure you want to submit?</h3> */}
            <p>Attempted Questions: {(userAnswers).filter(answer => answer !== null).length}</p>
            <p>Not Attempted Questions: {(questionsData).length - (userAnswers).filter(answer => answer !== null).length}</p>
          </Card.Text>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCloseModal}>
            Cancel
          </Button>
          <Button variant="primary" onClick={handleConfirmSubmit}>
            Confirm
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default QuestionData;
