

import React, { useState } from 'react';
import { Button, Card, Modal } from 'react-bootstrap';
import '../../styles/finalsubmissioncard.css';
import { useLocation, useNavigate } from 'react-router-dom';
import ResultCard from './ResultCard';

const FinalSubmissionCard = () => {
    const [showConfirmationModal, setShowConfirmationModal] = useState(false);
    const location = useLocation();
    const { userAnswers, questionsData, onConfirm, onCancel } = location.state;
    const navigate = useNavigate()
    // console.log("questionsDataquestionsData",questionsData.questionsData)
    // console.log("userAnswersuserAnswers", userAnswers.userAnswers)

    const handleConfirmClick = () => {
        // Close the modal

        navigate('/ResultCard', { state: { userAnswers: userAnswers, questionsData: questionsData } })
        // setShowConfirmationModal(false);
        // Call the onConfirm function
        // onConfirm();
    };

    return (
        <div className='FSC'>
            <div className='FSC-row'>
                {questionsData.questionsData.length > 0 ? (
                    <Card>
                        <Card.Body>
                            <h2>Final Submission</h2>
                            <Card.Text>
                                <h3>Are you sure you want to submit?</h3>
                                <p>Attempted Questions: {(userAnswers.userAnswers).filter(answer => answer !== null).length}</p>
                                <p>Not Attempted Questions: {(questionsData.questionsData).length - (userAnswers.userAnswers).filter(answer => answer !== null).length}</p>
                            </Card.Text>
                        </Card.Body>
                    </Card>
                ) : (
                    // Display a message or placeholder when questionsData is empty
                    <p>No questions available for submission.</p>
                )}

                <div className='FSC-buttonrow'>
                    <Button variant="primary" type='button' onClick={() => setShowConfirmationModal(true)}>
                        Confirm
                    </Button>
                    <Button variant="secondary" type='button' onClick={onCancel}>
                        Cancel
                    </Button>
                </div>

                {/* Confirmation Modal */}
                <Modal show={showConfirmationModal} onHide={() => setShowConfirmationModal(false)}>
                    <Modal.Header closeButton>
                        <Modal.Title>Confirm Submission</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <p>Are you sure you want to submit your answers?</p>
                        {/* <p>Attempted Questions: {(userAnswers.userAnswers).filter(answer => answer !== null).length}</p>
                        <p>Not Attempted Questions: {(questionsData.questionsData).length - (userAnswers.userAnswers).filter(answer => answer !== null).length}</p> */}
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={onCancel}>
                            Cancel
                        </Button>
                        <Button variant="primary" onClick={handleConfirmClick}>
                            Confirm
                        </Button>
                    </Modal.Footer>
                </Modal>
            </div>
        </div>
    );
};

export default FinalSubmissionCard;
