// App.js
import "../styles/user.css";
import { Button } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import UserAccess from "./user/qAndA";
import Dashboard from "./user/dashboardAfterLogin";
import { PublicClientApplication, InteractionType } from "@azure/msal-browser";
import {
  MsalProvider,
  AuthenticatedTemplate,
  UnauthenticatedTemplate,
} from "@azure/msal-react";
import { useMsal } from "@azure/msal-react";
import { msalConfig } from "../msalInstance";
import { useState } from "react";
import HeaderFor from "./header";

const pca = new PublicClientApplication(msalConfig);

function User() {
  const navigate = useNavigate();
  const [adminAccess,setAdminAccess]=useState('')
  const { instance, accounts, inProgress } = useMsal();

  const handleLogin = async () => {
    // Trigger login redirect

    try {
      const loginResponse = await pca.loginPopup();
      console.log("loginResponse", loginResponse);
      const objId = loginResponse.account.tenantId;
      const accessToken = loginResponse.accessToken;
      const email = loginResponse.account.username;
    //   const AdminListEmail = [
    //     "abhishekvardhan.danagalla@quadranttechnologies.com",
    //     "sivaprasad.polepally@quadranttechnologies.com",
    //     "rohan.devagiri@quadranttechnologies.com",
    //   ];
      console.log("email", email);
      console.log("objId", objId);

      // setData(response.data);

      const response = await fetch(
        `https://quickapplication.azurewebsites.net/api/value/VerifyAdminAccess?emailID=${email}`
      );
      if (!response.ok) {
        throw new Error("Network response was not ok");
      }
      const responseData = await response.json();
      const access=responseData.Data.HasAdminAccess
      setAdminAccess(access)
      
      if(access===true ||access===false ){
        localStorage.clear(); // Clear all items in local storage
        sessionStorage.clear();
        localStorage.setItem("access",access)
        localStorage.setItem("loader",1)
        navigate("/Dashboard");
       
        
      }
     
    
    } catch (error) {
      console.error("Error fetching data:", error);
    } finally {
      // setLoading(false)
    }
  };
  return (
    <MsalProvider instance={pca} interactionType={InteractionType.Redirect}>
      <div>
        <AuthenticatedTemplate>
          {/* Your authenticated content */}
          {/* <meta http-equiv="refresh" content="5;url=https://ms.portal.azure.com/" /> */}
        </AuthenticatedTemplate>
        <UnauthenticatedTemplate>
          <div className="background-image">
            <div className="login-container">
              <div className="UserForm">
                <label>
                  <b>Email Address</b>
                </label>
                <input
                  className="inputs"
                  type="email"
                  placeholder="Enter email"
                />
                <p></p>
                <label>
                  <b>Password</b>
                </label>
                <input
                  className="inputs"
                  type="password"
                  placeholder="Enter Password"
                />
                <p></p>
                <Button variant="primary" onClick={handleLogin}>
                  Login Now
                </Button>

                {/* Microsoft Login Button */}

                {/* <Button variant="primary" onClick={() => pca.loginRedirect()}>Sign In</Button> */}
              </div>
            </div>
          </div>
        </UnauthenticatedTemplate>
      </div>
    </MsalProvider>
  );
}

export default User;

