import React, { useState, useEffect } from "react";
import "../../styles/addRemove.css";
import { Form, Button, InputGroup, FormControl } from "react-bootstrap";
import { Alert } from "react-bootstrap";

const TopicWithPercent = (props) => {
  const { formData, setFormData } = props;
  const [inputSets, setInputSets] = useState([{ topic: "", percentage: "" }]);
  const [msg, setMsg] = useState();

  useEffect(() => {
    // Parse TechnologiesPercentage from formdata
    console.log("formData-one", formData);
    if (formData.name !== "") {
      const parseTechnologiesPercentage = (formData) => {
        console.log("formData-two", formData);
        const technologiesPercentage = formData?.technologiesPercentage;
        const pairs = technologiesPercentage?.split(",");
        const sets = [];

        for (let i = 0; i < pairs.length; i += 2) {
          sets.push({ topic: pairs[i], percentage: pairs[i + 1] });
        }

        return sets;
      };

      // Set inputSets when formdata changes
      setInputSets(parseTechnologiesPercentage(formData));
    }
  }, [formData?.name]);

  useEffect(() => {
    const newTopics = inputSets.map(({ topic }) => topic);
    const newPercentages = inputSets.map(({ percentage }) => percentage);

    const totalPercentage = newPercentages.reduce((total, percentage) => {
      const numPercentage = Number(percentage);
      console.log("numPercentagenumPercentage", numPercentage);
      const totalSum = total + Number(percentage);

      return !isNaN(numPercentage) ? total + numPercentage : total;
    }, 0);
    console.log(
      "totalPercentagetotalPercentagetotalPercentageeeeeeeeeeeee",
      totalPercentage
    );

    if (totalPercentage <= 100) {
      // setFormData((prevFormData) => ({
      //   ...prevFormData,
      //   topics: newTopics,
      //   percentages: newPercentages,
      // }));
    } else {
      // If the total exceeds 100, remove the last input set
      // setInputSets((prevInputSets) => prevInputSets.slice(0, -1));
      alert(
        "sum of total percentage shoud not be more then 100:",
        totalPercentage
      );
    }
  }, [inputSets, setFormData]);

  const addInputSet = () => {
    // setInputSets((prevInputSets) => [...prevInputSets, { topic: '', percentage: '' }]);
    if (inputSets.length < 4) {
      setInputSets((prevInputSets) => [
        ...prevInputSets,
        { topic: "", percentage: "" },
      ]);
    } else {
      alert("You cannot add more than 4 input sets.");
     
    }
  };

  const removeInputSet = (index) => {
    setInputSets((prevInputSets) => {
      const newInputSets = [...prevInputSets];
      newInputSets.splice(index, 1);
      return newInputSets;
    });
  };

  const updateInputSet = (index, field, value) => {
    setInputSets((prevInputSets) => {
      const newInputSets = [...prevInputSets];
      newInputSets[index][field] = value;
      return newInputSets;
    });
  };

  const isPercentageValid = (percentage) => {
    const numPercentage = Number(percentage);
    return !isNaN(numPercentage) && numPercentage >= 0 && numPercentage <= 100;
  };

  useEffect(() => {
    setFormData(inputSets);
  }, [inputSets]);

  return (
    <div style={{ display: "flex", flexDirection: "column" }}>
      {inputSets.map((inputSet, index) => (
        <div key={index} className="input-top-perc">
          <input
            type="text"
            placeholder="  Topic "
            value={inputSet?.topic}
            onChange={(e) => updateInputSet(index, "topic", e.target.value)}
            className="input-topic"
          />
          <input
            type="text"
            placeholder="  Percentage "
            value={inputSet?.percentage}
            onChange={(e) => {
              const newPercentage = e.target.value;
              if (isPercentageValid(newPercentage)) {
                updateInputSet(index, "percentage", newPercentage);
              }
            }}
            className="input-perc"
          />{" "}
          <span className="perc-symbol">%</span>
          <Button
            type="button"
            variant="warning"
            onClick={addInputSet}
            className="add-button"
          >
            Add
          </Button>
          {index !== 0 && (
            <Button
              variant="danger"
              onClick={() => removeInputSet(index)}
              className="btn-remove"
              type="button"
            >
              Remove
            </Button>
          )}
          {!isPercentageValid(inputSet.percentage) && (
            <span style={{ color: "red" }}>{msg}</span>
          )}
        </div>
      ))
      }
     
    </div>
  );
};

export default TopicWithPercent;

