import React, { useState, useEffect } from "react";
import { Table, Button, Spinner } from "react-bootstrap";
import { useNavigate } from "react-router-dom";

import "../../styles/dashboard.css";

const Dashboard = () => {
  const navigate = useNavigate();
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [hasReloaded, setHasReloaded] = useState(false);
  // const [examId, SetExamId] = useState();

  

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true); // Set loading to true when starting the fetch
        const response = await fetch(
          "https://quickapplication.azurewebsites.net/api/value/getExamDetails"
        );
        const result = await response.json();
        // console.log("Eaxm", result)
        
        setData(result);
        if(!hasReloaded){
            console.log("reloaddddddddddddd")
            setHasReloaded(true)
        }
      } catch (error) {
        console.error("Error fetching data:", error);
      } finally {
        setLoading(false);

       
      }
    };

    fetchData();
  }, []);


  useEffect( () => {
    const loader = localStorage.getItem('loader')
    if(loader){
        window.location.reload();
        localStorage.removeItem('loader')
    }
  })
  

  const handleBookSlot = () => {
    navigate("/UserAccess");
  };

  const handleStartExam = async (examDate, ExamId, Id) => {
    const currentDate = new Date();
    const formattedCurrentDate = currentDate.toLocaleDateString("sv-SE", {
      year: "numeric",
      month: "numeric",
      day: "numeric",
    });
    const formattedExamDate = new Date(examDate).toLocaleDateString("sv-SE", {
      year: "numeric",
      month: "numeric",
      day: "numeric",
    });

    console.log("IDdddddddddd", Id);
    // const userData = await fetchUserDataById(Id.ExamId);
    // console.log("userDataaaaaaaaaaaa", userData[0])

    if (formattedExamDate === formattedCurrentDate) {
      // Navigate to the TermsConditions page
      const det = JSON.stringify(Id);
      navigate("/TermsCondition/" + ExamId, { state: { examInfo: Id } });

      // localStorage.setItem("ExamDetails", det);
    } else {
      // Show a message that the exam is not on the current date
      alert("Your exam is on a different date.");
    }
  };

  const handelResponse = (Id) => {
    navigate("/response", { state: { Id: Id } });
  };

  const tableHeaderStyle = {
    backgroundColor: "#203961", // Set the background color
    color: "#ffffff", // Set the text color
    padding: "10px", // Add some padding
    fontFamily: "roboto",
  };
  const containerStyle = {
    minHeight: "100vh",
    // backgroundColor: 'gray',
    backgroundColor: "aliceblue",
    padding: "10px",
  };

  return (
    <div style={containerStyle}>
      <div className="Dash-button">
        {/* style={{ backgroundColor: '#7BBEE2' }} */}
        <Button
          variant="info"
          onClick={handleBookSlot}
          style={{ color: "#fff" }}
        >
          <b>Book Slot</b>
        </Button>
      </div>
      <div>
        {loading ? (
          <Spinner animation="border" role="status">
            <span className="sr-only"></span>
          </Spinner>
        ) : (
          <div style={{ padding: "15px" }}>
            <Table responsive striped="row" bordered hover size="sm">
              <thead>
                <tr>
                  <th style={tableHeaderStyle}>ID</th>
                  <th style={tableHeaderStyle}>Exam Name</th>
                  <th style={tableHeaderStyle}>Date</th>
                  <th style={tableHeaderStyle}>Result (%)</th>
                  {/* <th>Result</th> */}
                  <th style={tableHeaderStyle}>Action</th>
                  <th style={tableHeaderStyle}>Response</th>
                </tr>
              </thead>
              <tbody>
                {data.map((item) => (
                  <tr key={item.Id} style={{ fontFamily: "segoe UI" }}>
                    <td>{item.Id}</td>
                    {/* <td>{item.ExamId}</td> */}
                    <td>{item.ExamName}</td>
                    <td>{item.Exambookeddate.split("T")[0]}</td>
                    {/* <td><b>{item.Result}%</b></td> */}
                    <td>
                      <b>{item.Result ? item.Result + "%" : ""}</b>
                    </td>
                    <td>
                      <Button
                        variant="success"
                        size="sm"
                        onClick={() =>
                          handleStartExam(
                            item.Exambookeddate,
                            item.ExamId,
                            item
                          )
                        }
                      >
                        Start
                      </Button>
                    </td>
                    <td>
                      <Button
                        variant="primary"
                        size="sm"
                        style={{ color: "#fff" }}
                        onClick={() => handelResponse(item.Id)}
                      >
                        <b>Review</b>
                      </Button>
                    </td>
                    {/* <Link to={`/Response/${item.ExamId}`}>
                                            <Button variant="info" size="sm" onClick={() => handleViewResponse(item.ExamId)}>
                                                Response
                                            </Button>
                                        </Link> */}
                  </tr>
                ))}
              </tbody>
            </Table>
          </div>
        )}
      </div>
    </div>
  );
};

export default Dashboard;
