


import React, { useState } from 'react';
import { Button, Col, Row } from 'react-bootstrap';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

function SlotBook({ onSelect }) {
    const [selectedStartDate, setSelectedStartDate] = useState(null);

    const isThursday = date => date.getDay() === 4;

    const handleDateStartSelection = date => {
        setSelectedStartDate(date);
        console.log("Dateeeeeeeee",date)
    };

    const handleSelect = () => {
        onSelect({selectedStartDate});
    };

    return (
        <div>
            <Row>
                <Col md={6} lg={4}>
                    <label>Select Data</label>
                    <DatePicker
                        selected={selectedStartDate}
                        onChange={handleDateStartSelection}
                        minDate={new Date()}
                        dateFormat='dd/MM/yyyy'
                        filterDate={isThursday}
                        className="form-control"
                    />
                </Col>
                <Col sm={12} md={6} lg={6} style={{ marginTop: '10px' }}>
                    <Button variant='primary' style={{ marginTop: '5px', display: 'flex', flexDirection: 'row', justifyContent: 'center' }} onClick={handleSelect}>
                        Confirm
                    </Button>
                </Col>
            </Row>
        </div>
    );
}

export default SlotBook;
