import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Button, Table } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import fetchUserDataById from './api/userDataApi';
import Spinner from 'react-bootstrap/Spinner';

function AdminDashboard(props) {
    const [formData, setFormData] = useState(null);
    const navigate = useNavigate();
    useEffect(() => {
        fetch('https://quickapplication.azurewebsites.net/api/value/getAdmindetails')
            // Replace with your API endpoint
            .then(response => response.json())
            .then(data => {
                const reversedData = data.reverse();


                setFormData(reversedData);
            })

    }, []);
    let handleEditClick = (userId) => {
        console.log(userId)

        navigate(`/Admin/${userId}`);


    };
    const handleDeleteClick = (userId) => {
        fetch(`https://quickapplication.azurewebsites.net/api/value/deleteUserbyid?id=${userId}`)
            .then(response => response.json())
            .then(deletedUser => {
                // Update the state to remove the deleted user
                setFormData(prevData => prevData.filter(user => user.Id !== userId));
                console.log('User deleted successfully:', deletedUser);
            })
            .catch(error => {
                console.error('Error deleting user:', error);
            });
    };

    if (!formData) {
        // return <div>Loading...</div>;
        return <div>
            <Spinner animation="border" />;
        </div>
    }
    const tableHeaderStyle = {
        backgroundColor: '#3498db',
        color: '#ffffff',
        padding: '10px',

    };
    return (
        <div>
            <h2 style={{ margin: '10px' }}>Welcome to the Admin Dashboard</h2>
            <br></br>
            {/* <p>Here is the information you entered:</p> */}

            <Table striped bordered hover >
                <thead >
                    <tr >
                        <th style={tableHeaderStyle} >Id</th>
                        <th style={tableHeaderStyle}>Name</th>
                        <th style={tableHeaderStyle}>Technologies</th>
                        <th style={tableHeaderStyle}>Weightage</th>
                        <th style={tableHeaderStyle}>Complexity</th>
                        <th style={tableHeaderStyle}>No. of Questions</th>
                        <th style={tableHeaderStyle}>Time Duration</th>
                        <th style={tableHeaderStyle}>Created Date</th>
                        <th style={tableHeaderStyle}>Pass Percentage</th>
                        <th style={tableHeaderStyle}>Internet Usage</th>
                        {formData.internetRadio === 'NO' && <th>Uploaded Document</th>}
                        <th style={tableHeaderStyle}>Action</th>
                    </tr>
                </thead>
                <tbody>
                    {formData && formData.map(item => (
                        <tr key={item.Id}>
                            <td>{item.Id}</td>
                            <td>{item.Name}</td>
                            <td>{item.Technologies}</td>
                            <td>{item.TechnologiesPercentage}</td>
                            <td>{item.Complexity}</td>
                            <td>{item.NoOfQuestions}</td>
                            <td>{item.TimeDuration}</td>
                            <td>{item.CreatedBy}</td>
                            <td>{item.PassPercentage}</td>
                            <td>{String(item.Internetreqirement)}</td>
                            <td>
                                {/* <Link to={'/Admin'} state={{ formData: item.id }}>
                                    <Button variant='primary' onClick={() => handleEditClick(item.Id)}>Edit</Button>
                                </Link> */}
                                <Link to={`/Admin/${item.Id}`}>
                                    <Button variant='primary' onClick={() => handleEditClick(item.Id)}>Edit</Button>
                                </Link>

                                <Link>
                                    <Button variant='danger' onClick={() => handleDeleteClick(item.Id)}>Delete</Button>
                                </Link>
                            </td>

                        </tr>
                    ))}
                </tbody>
            </Table>
            <Link to="/Admin">
                <Button variant='primary'>BACK</Button>
            </Link>

        </div>)
}

export default AdminDashboard;


