

import React, { useState } from "react";
import '../../styles/termsCondition.css'
import { useNavigate, useParams } from "react-router-dom";
// import QuestionData from "./qAndA";
import './questionsData.json'
import { Spinner } from 'react-bootstrap';
import { Container, Row, Col, Form, Button } from "react-bootstrap";

import { useLocation } from "react-router-dom";

const TermsCondition = (props) => {
    const { userId } = useParams();
    const [examQA,setExamQA]=useState()
    const location=useLocation()
    const {examInfo}=location.state

    console.log("sssssssssssssssssssssssssssssssssssssssssss", userId)
    const navigate = useNavigate();
    const [isChecked, setIsChecked] = useState(false);
    const [loading, setLoading] = useState(false);

   
    const fetchUserDataById = async (userId) => {
        try {
            const response = await fetch(`https://quickapplication.azurewebsites.net/api/value/getUserbyid?id=${userId}`);
            const userData = await response.json();
            return userData;


        } catch (error) {
            console.error('Error fetching user data by ID:', error);
            return null;
        }
    };

    // export default fetchUserDataById;

    const handleLogin = async () => {
        
        try {
            setLoading(true);    
            if (isChecked) {
                // localStorage.getItem('qt').length !== 0
                if (examQA.length!==0) {
                    navigate('/QuestionData/' + userId,{state:{examInfo:examInfo,examQA:examQA}});
                }

            } else {
                // Handle the case where the checkbox is not checked
                // You may display an error message or take appropriate action
                await handleCheckboxChange();
            }
        } catch {
            console.log("wating for res")
        } 
        finally {
            setTimeout(() => {
                setLoading(false);
            }, 10000)};

    };

    const handleCheckboxChange = async (e) => {
        try {
            setIsChecked(e.target.checked);
            // Fetch user data
            const userData = await fetchUserDataById(userId);
            console.log("userDataaaaaaaaaaaa", userData)

            // Log or handle the user data as needed
            console.log("userdata", userData[0].Internetreqirement);

            // Update the checkbox state
            if (userData && userData[0].Internetreqirement) {
                // If InternetRequirement is true, make a POST request to the first endpoint
                const formData = new FormData();
                formData.append('course_name', userData[0].Technologies)
                formData.append('num_questions', userData[0].NoOfQuestions)
                formData.append('input_topics', userData[0].TechnologiesPercentage)
                formData.append('difficulty_level', userData[0].Complexity)
                const response = await fetch('http://20.235.2.210:8000/generate_question_paper/gpt', {
                    // http://40.78.194.97/generate_question_paper/gpt
                    // http://10.0.8.19:8080/generate_question_paper/gpt
                    // http://20.235.2.210:8000/generate_question_paper/gpt
                    method: 'POST',
                    body: formData,

                });

                const data = await response.json();
                // console.log("dataaaaaaaaaaaaaaaaaaaaaaaaa", data.questions.length)

                if(userData[0].NoOfQuestions===(data.questions).length){
                    // localStorage.setItem('qt', JSON.stringify(data['questions']))
                }
                
                setExamQA(data.questions)
               
                // localStorage.setItem('qt', JSON.stringify(data['questions']))




            } else {
                // Fetch data from the second endpoint or handle as needed
                const formData = new FormData();
                formData.append('course_name', userData[0].Technologies)
                formData.append('num_questions', userData[0].NoOfQuestions)
                formData.append('input_topics', userData[0].TechnologiesPercentage)
                formData.append('difficulty_level', userData[0].Complexity)
                const response = await fetch('http://20.235.2.210:8000/generate_question_paper/index', {
                    // http://10.0.8.19:8080/generate_question_paper/index
                    method: 'GET',
                    // body: formData,
                });

                const data = await response.json();

                localStorage.setItem('qt', JSON.stringify(data))
                console.log('Fetching data from the second endpoint or handling as needed');
                
            }
        } catch (error) {
            console.error('Error fetching user data:', error);
            
            // Handle the error as needed
        }finally {
            setLoading(false);
        }
    };

    return (
        <div style={{display:"flex",flexDirection:'row',justifyContent:'center'}}>

            <Container className="terms">
                <h1 style={{color:"#ffffff"}}>Terms and Conditions</h1>
                <Row>
                    <Col>
                        <div className="terms-context">
                            <ul className="TC-li" style={{fontFamily:'roboto',fontSize:'38px',padding:'5px'}}>
                                <li>You must use a functioning webcam and microphone.</li>
                                <li>No cell phones or other secondary devices in the room or test area.</li>
                                <li>No one else can be in the room with you.</li>
                                <li>No dual screens/monitors</li>
                                <li>Do not leave the camera's field of view at any time during the exam.</li>
                                <li>No use of additional applications or internet</li>
                                <li>No other devices in view during this exam.</li>
                                <li>Maintain silence throughout the entire duration of the exam.</li>
                                <li>Your desk/table must be clear or any materials except your test-taking device.</li>
                            </ul>
                        </div>
                    </Col>
                </Row>

                <Row>
                    <Col>
                        <div className="tacbox">
                            <Form.Check style={{color:'yellow'}}
                                type="checkbox"
                                id="checkbox"
                                label="I agree to these Terms and Conditions."
                                onChange={handleCheckboxChange}
                                checked={isChecked}
                                required
                            />
                        </div>
                    </Col>
                </Row>
                {/* 
                <Row>
                    <Col>
                        <div className="flex-container">
                            <Button variant="primary" onClick={handleLogin}>
                                Submit
                            </Button>
                            
                        </div>
                    </Col>
                </Row> */}
                <Row>
                    <Col>
                        <div className="flex-container">
                            <Button variant="info" onClick={handleLogin}>
                                {loading ? (
                                    <Spinner animation="border" size="sm" />
                                ) : (
                                    'Submit'
                                )}
                            </Button>
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

export default TermsCondition;
