import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import SlotBook from "./slotBook";
import { Card, Form, Container, Row, Col } from 'react-bootstrap';
import "react-datepicker/dist/react-datepicker.css";
import "../../styles/userAccess.css";

function UserAccess() {
  const navigate = useNavigate();
  const [isSlotBooked, setIsSlotBooked] = useState(false);
  // const [bookingDetails, setBookingDetails] = useState([]);
  const [selectedTechnology, setSelectedTechnology] = useState("Select Tech");
  const [idMap, setIdMap] = useState([]);
  const [technologies, setTechnologies] = useState([]); // Initialize with an empty array

  useEffect(() => {
    // Fetch technologies dropdown from the API
    const fetchTechnologies = async () => {
      try {
        const response = await fetch(
          "https://quickapplication.azurewebsites.net/api/value/getExamDropdown"
        );
        if (response.ok) {
          const data = await response.json();
          setTechnologies(["Select Tech", ...data.map((item) => item.Name)]);

          const idMap = new Map(data.map((item) => [item.Name, item.Id]));

          // Store the ID map in a state
          setIdMap(idMap);

          // setTechnologies(technologies);
        } else {
          console.error(`Failed to fetch technologies: ${response.status}`);
        }
      } catch (error) {
        console.error("Error:", error);
      }
    };

    fetchTechnologies();
  }, []);

  const handleBooking = async ({ selectedStartDate }) => {
    console.log("startdate", selectedStartDate);
    // console.log("selectedEndDate", selectedEndDate);

    if (selectedTechnology !== "Select Tech") {
      try {
        const selectedExamId = idMap.get(selectedTechnology);
        var newdate = selectedStartDate.toLocaleDateString("sv-SE", {
          year: "numeric",
          month: "numeric",
          day: "numeric",
        });
        console.log("startdateeeeeeeeeeeee", newdate);
        let payload = JSON.stringify({
          Id: "0",
          ExamId: selectedExamId,
          ExamName: selectedTechnology,
          Exambookeddate: newdate,
          Result: "", // You can set a default value or handle this as needed
          ExamEndTime: selectedStartDate,
          ExamResonse: "",
          UserAnswers: "",
          CreatedBy: "abhi", // You can set a default value or handle this as needed
        });
        const response = await fetch(
          "https://quickapplication.azurewebsites.net/api/value/CreateorUpdateExamuser",
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: payload,
          }
        );

        if (response.ok) {
          setIsSlotBooked(true);
          // setBookingDetails({
          //     date: selectedStartDate
          //     time: `${selectedTime.start} - ${selectedTime.end}`,
          // });
          navigate("/Dashboard"); // Directly navigate to the dashboard after successful booking
        } else {
          console.error(`Failed to book slot: ${response.status}`);
        }
      } catch (error) {
        console.error("Error:", error);
      }
    } else {
      console.error("Please select a date and exam before booking.");
    }
  };

  return (
    <>
      <div style={{backgroundColor:'aliceblue', height:'100vh' }}>
        <div style={{display:'flex',flexDirection:'column',justifyContent:'center'}}>
          {/* <div className='User-name'>
                        <label> Select Exam </label>
                        <select
                            id={selectedTechnology.Id}
                            className='inputs'
                            value={selectedTechnology}
                            onChange={(e) => setSelectedTechnology(e.target.value)}
                        >
                            {technologies.length > 0 && technologies.map((tech, index) => (
                                <option key={index} value={tech}>
                                    {tech}
                                </option>
                            ))}
                        </select>
                    </div> */}
          {/* <div>
                        <Form.Label>Select Exam</Form.Label>
                        <Form.Select
                            id={selectedTechnology.Id}
                            className='inputs'
                            value={selectedTechnology}
                            onChange={(e) => setSelectedTechnology(e.target.value)}
                        >
                            {technologies.length > 0 && technologies.map((tech, index) => (
                                <option key={index} value={tech}>
                                    {tech}
                                </option>
                            ))}
                        </Form.Select>
                    </div>
                    <div className='slot'>
                        {isSlotBooked ? (
                            <div>
                                <p>Slot is booked!</p>
                            </div>
                        ) : (
                            <>
                                <label> Slot Booking: </label>
                                <SlotBook onSelect={handleBooking} />

                            </>
                        )}
                    </div> */}
          <Container >
            <Row className="justify-content-center mt-5" >
              <Col xs={12} md={8} lg={6}>
                <Card className="shadow">
                  <Card.Body>
                    <div>
                      <Form.Label as='h5'>Select Exam</Form.Label>
                      <Form.Select
                        id={selectedTechnology.Id}
                        className="inputs"
                        value={selectedTechnology}
                        onChange={(e) => setSelectedTechnology(e.target.value)}
                      >
                        {technologies.length > 0 &&
                          technologies.map((tech, index) => (
                            <option key={index} value={tech}>
                              {tech}
                            </option>
                          ))}
                      </Form.Select>
                    </div>
                    <div className="slot">
                      {isSlotBooked ? (
                        <div>
                          <h5>Slot is booked!</h5>
                        </div>
                      ) : (
                        <>
                          {/* <label as='h5'> Slot Booking: </label> */}
                          <Form.Label as='h5'> Slot Booking: </Form.Label>
                          <SlotBook onSelect={handleBooking} />
                        </>
                      )}
                    </div>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
      </div>
    </>
  );
}

export default UserAccess;
