// Assume this function fetches user data from the API by user ID
const fetchUserDataById = async (userId) => {
    try {
        const response = await fetch(` https://quickapplication.azurewebsites.net/api/value/getUserbyid?id=${userId}`);
        // https://quickapplication.azurewebsites.net/api/value/deleteUserbyid
        // https://quickapplication.azurewebsites.net/api/value/getUserbyid
        // https://localhost:44311/api/value/getUserById?id=${userId}
        const userData = await response.json();
        return userData;
       
    } catch (error) {
        console.error('Error fetching user data by ID:', error);
        return null;
    }
};

export default fetchUserDataById;
