


import React, { useState, useEffect } from 'react';
// import { useParams } from 'react-router-dom'; 
import { useLocation, useNavigate } from 'react-router-dom';
import { Button, Spinner,Card } from 'react-bootstrap';
// import Dashboard from './dashboardAfterLogin';

const ExamResponse = () => {
    const location = useLocation()
    const navigate = useNavigate()
    const [examDetails, setExamDetails] = useState(null);
    const { Id } = location.state

    console.log("iddddddddddddddddddd", Id)

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('https://quickapplication.azurewebsites.net/api/value/getExamDetails');
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }

                const data = await response.json();

                setExamDetails(data);

            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData();
    }, []);

    if (!examDetails) {
        return (
            <div style={{
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                height: '100vh',

            }}>
                <Spinner animation="border" role="status" size="lg">
                    <span className="sr-only"></span>
                </Spinner>
            </div>

        );
    }

    const handelBack = () => {
        navigate('/Dashboard')
    }

    // Filter only responses with a specific ID, e.g., '139'
    const filteredResponses = examDetails.filter(response => response.Id === Id);
    console.log("filteredResponsesfilteredResponsesfilteredResponsesfilteredResponses",filteredResponses)

    if (filteredResponses.length === 0) {
        // No filtered data available, show message
        return (
            <div>
                <p>You have not attempted this exam.</p>
                <Button variant='success' onClick={handelBack}>Back</Button>
            </div>
        );
    }

    let userAns;
    let examResponseData;

    try {
        userAns = JSON.parse(filteredResponses[0].UserAnswers);
        examResponseData = JSON.parse(filteredResponses[0].ExamResonse);
    } catch (error) {
        console.error('Error parsing JSON:', error);
        return (
            <div >
                <p style={{ textAlign: 'center', padding: "5px" }}>There was an error processing the exam data.</p>
                <div>
                    <Button variant='success' onClick={handelBack}>Back</Button>
                </div>
            </div>
        );
    }

    // const userAns =JSON.parse( filteredResponses[0].UserAnswers)
    // const examResponseData = JSON.parse(filteredResponses[0].ExamResonse);
    console.log("const { id } = useParams;", filteredResponses[0].ExamResonse)
    console.log(userAns)



    const respoContainer = {
        backgroundColor: 'aliceblue',//cadetblue
        color: '#ffffff',
        padding: '20px',
        color:'black',
    };
    return (
        <div style={respoContainer}>
            {/* <h1>Exam Details</h1> */}
          
                {filteredResponses.map((response, index) => (
                    <Card key={index} style={{ width: '18rem', marginBottom: '20px' }}>
                        <Card.Body>
                            <Card.Title><b>ID:</b> {response.Id}</Card.Title>
                            <Card.Text><b>Exam Name:</b> {response.ExamName}</Card.Text>
                            <Card.Text><b>Result:</b> {response.Result}</Card.Text>
                            {/* Add other fields as needed */}
                        </Card.Body>
                    </Card>
                ))}
          
            <h2>Exam Response</h2>

            {examResponseData.map((question, index) => (
                <ul key={index}>
                    <li key={index} style={{listStyle:'none'}}>
                        <p style={{ fontWeight: 'bold' }}>{` ${question.question_number}: ${question.question_text}`}</p>
                        <ul>
                            {/* {question.options.map((option, optionIndex) => (
                                <li   key={optionIndex}>{option+userAns[index]}</li>
                            ))} */}
                            {question.options.map((option, optionIndex) => {
                                const userAnswer = userAns[index];
                                console.log("userAnswer", userAnswer)
                                const isUserAnswerCorrect = userAnswer === option;

                                const optionStyle = {
                                    color: isUserAnswerCorrect ? 'blue' : 'black',
                                    fontWeight: isUserAnswerCorrect ? 'bold' : 'normal',
                                    optionStyle: isUserAnswerCorrect ? "none" : ''
                                };

                                return (
                                    <li key={optionIndex} style={optionStyle}>
                                        
                                        {`${option} ${userAnswer === option ? '(seletecd option)' : ''}`}
                                    </li>
                                    // <li key={optionIndex} style={optionStyle}>
                                    //     <label>
                                    //         <input
                                    //             type="radio"
                                    //             name="answerOptions" // Use the same name for all radio buttons in a group
                                    //             value={option}
                                    //             defaultChecked={isUserAnswerCorrect}

                                    //         />
                                    //         {`${option} ${userAnswer === option ? '(Your Answer)' : ''}`}
                                    //     </label>
                                    // </li>

                                );
                            })}
                        </ul>
                        <p>{`Correct Answer: ${question.correct_answer}`}</p>
                    </li>
                </ul>
            ))}



            <Button variant='success' onClick={handelBack}>Back</Button>
        </div>
    );
};

export default ExamResponse;
