// export default HeaderFor;

import React, { useEffect, useState } from "react";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import {
  BrowserRouter as Router,
  Route,
  Routes,
  NavLink,
} from "react-router-dom";
import AdminForm from "./admin";
import User from "./user";
import AdminDashboard from "./adminDashboard";
import UserAccess from "./user/userAccess";
import SlotBook from "./user/slotBook";
import TermsCondition from "./user/TermsCondition";
import QuestionData from "./user/qAndA";
import Dashboard from "./user/dashboardAfterLogin";
import ConfirmationPage from "./user/confirmationPage";
import FinalSubmissionCard from "./user/FinalSubmissionCard";
import ResultCard from "./user/ResultCard";
import ExamResponse from "./user/examResponse";
import Logout from "./logoutPage";
import "../styles/header.css";

function HeaderFor() {
  const [isAdmin, setIsAdmin] = useState(false);
  const [isUser, setIsUser] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  useEffect(() => {
    const adminAuthState = localStorage.getItem("access");
    console.log();
    if (adminAuthState === "true") {
      console.log("adminAuthStateadminAuthState", adminAuthState);
      setIsLoggedIn(true);
      setIsAdmin(true);
      // setIsUser(true);
    } else if (adminAuthState === "false") {
      console.log("inside elif", adminAuthState);
      setIsLoggedIn(true);
      setIsUser(true);
      setIsAdmin(false);
    } else {
      console.log("inside else", adminAuthState);
      setIsLoggedIn(false);
      setIsUser(false);
    }
  }, []);

  const handleLogout = () => {
    // pca.logout();
    console.log("logout", isLoggedIn);
    window.location.reload(true); // Reload the page forcing a cache refresh

    // Clear local session
    localStorage.clear(); // Clear all items in local storage
    sessionStorage.clear();
    localStorage.removeItem("access");
    window.history.replaceState(null, "", "/logout");
    setIsLoggedIn(false);
    setIsAdmin(false);
    setIsUser(false);
    window.location.href = "/logout";
  };

  return (
    <Router>
      <div>
        <Navbar bg="light" expand="sm" className="sticky-top">
          <Navbar.Brand href="/">
            <img
              src="https://www.quadranttechnologies.com/images/logo.png"
              alt="Your Brand Logo"
              height="40"
              className="d-inline-block align-top"
            />
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ms-auto">
              {isLoggedIn && isAdmin && (
                <>
                  <NavLink to="/Admin" className="nav-link">
                    Admin
                  </NavLink>
                  <NavLink to="/Dashboard" className="nav-link">
                    User
                  </NavLink>
                </>
              )}
              {isLoggedIn && isUser && (
                <NavLink to="/Dashboard" className="nav-link">
                  User
                </NavLink>
              )}
              {isLoggedIn && <Nav.Link onClick={handleLogout}>Logout</Nav.Link>}
            </Nav>
          </Navbar.Collapse>
        </Navbar>

        <Routes>
          <Route path="/" element={<User />} index />
          {isLoggedIn ? (
            <Route path="/Admin/:userId" element={<AdminForm />} />
          ) : null}
          {isLoggedIn ? <Route path="/Admin" element={<AdminForm />} /> : null}
          {isLoggedIn ? (
            <Route path="/UserAccess" element={<UserAccess />} />
          ) : null}
          {isLoggedIn ? (
            <Route path="/slotBook" element={<SlotBook />} />
          ) : null}
          {isLoggedIn ? (
            <>
              <Route
                path="/TermsCondition/:userId"
                element={<TermsCondition />}
              />
              <Route path="/QuestionData/:userId" element={<QuestionData />} />
              <Route path="/ResultCard/:userId" element={<ResultCard />} />
              <Route
                path="/FinalSubmissionCard"
                element={<FinalSubmissionCard />}
              />

              <Route path="/response" element={<ExamResponse />} />
              <Route path="/adminDashboard" element={<AdminDashboard />} />
              <Route path="/Dashboard" element={<Dashboard />} />
            </>
          ) : null}
          <Route path="/Dashboard" element={<Dashboard />} />
          <Route path="/logout" element={<Logout />} />
          <Route
            path="/confirmation"
            element={<ConfirmationPage answers={[]} />}
          />
        </Routes>
      </div>
    </Router>
  );
}

export default HeaderFor;
