
import React, { useState, useEffect } from 'react';
import { Button } from 'react-bootstrap';
import { useNavigate, useLocation, useParams } from 'react-router-dom';
import fetchUserDataById from '../api/userDataApi'
// import User from '.';
// import Dashboard from './dashboardAfterLogin';
// import UserAccess from './userAccess';
import "../../styles/resultcard.css"

const ResultCard = () => {
    const [examDetails, setExamDetails] = useState(null);
    const [formData, setFormData] = useState();
    const [passPercent,setPassPercent]=useState()
    const location = useLocation();
    const { userId } = useParams()
    const { userAnswers, questionsData,examInfo } = location.state
    const user = userAnswers['userAnswers']
    console.log("userrrrrrrrrrrrrrrrrrrrrrrrrrrrrrr",user)
    // userAnswers.userAnswers[0].split('.')[0]
    const questions = questionsData.questionsData
    // console.log("userAnswers.userAnswersuserAnswersuserAnswers", user)
    // console.log("userAnswers.userAnswers", questions)
    // console.log("examInfoooooooooo",examInfo['examInfo'])

    // const getTargetPercentage=()=>{
    //     fetch('https://localhost:44311/api/value/getAdmindetails')
    //         // Replace with your API endpoint
    //         .then(response => response.json())
    //         .then(data => {
    //             const reversedData = data
    //             console.log("reversedDatareversedDatareversedData",reversedData)

    //             setFormData(reversedData);
    //         })
    // }


    useEffect(() => {
        const fetchData = async () => {
            // const storedExamDetails = localStorage.getItem("ExamDetails");
            const storedExamDetails=examInfo['examInfo']
            console.log("storedExamDetailsstoredExamDetailsstoredExamDetails",storedExamDetails)
            if (storedExamDetails) {
                //   
                try {
                    // Assuming fetchUserDataById returns a promise
                    const adminDataById = await fetchUserDataById(userId);
                    const percentage=adminDataById[0].PassPercentage
                    setPassPercent(percentage)
                    console.log("adminDataById  adminDataById();", adminDataById[0].PassPercentage);

                    // // Extract passPercentage from adminDataById
                    // const passPercentage = adminDataById.PassPercentage;
                    // console.log("passPercentage", passPercentage);
                    // console.log("JSON.parse(storedExamDetails)",(storedExamDetails))
                    setExamDetails((storedExamDetails));
                } catch (error) {
                    console.error("Error fetching user data:", error);
                }
            }
        }
        // getTargetPercentage()
        console.log("result formdata", formData)
        fetchData();
    }, []);

    const navigate = useNavigate()

    const calculateScore = () => {
        let correctCount = 0;

        user.forEach((user, index) => {

            console.log(index)
            const correctAnswer = questions[index].correct_answer;
            const QData = questions[index].question_text
            const qOptions = questions[index].options
            console.log("QData............QData", QData)
            console.log("correctAnswer", correctAnswer)
            console.log("qOptionsqOptions", qOptions)

            if (user != null) {
                console.log("inside" ,user)
                               // console.log("after" ,user)
                if (user === correctAnswer) {
                    correctCount++;
                }
            }

        });

        const score = (correctCount / questions.length) * 100;
        console.log("scoreeeeeeeeeeeeeeeee",score)
        return score.toFixed(1); // Limit to two decimal places
    };

    const renderMessage = (score) => {
        if (score >= passPercent ) {
            return <p style={{fontSize:"24px", fontFamily:'monospace'}}>Congratulations! You passed the quiz with a percentage of <b>{score}%</b></p>;
        } else {
            return <p>Better luck next time. Your percentage is <b>{score}%.</b></p>;
        }
    };

    const score = calculateScore();

    const handleExit = () => {

        if (examDetails) {
            const updatedExamDetails = {
                ...examDetails,
                Result: calculateScore(),
                ExamResonse: JSON.stringify(questions).toString(),
                UserAnswers: JSON.stringify(user).toString(),
                ExamEndTime: new Date().toISOString(),
            };
            fetch('https://quickapplication.azurewebsites.net/api/value/CreateorUpdateExamuser', {
                method: 'POST', // Assuming it's a POST request, change accordingly
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(updatedExamDetails),
            })
                .then(response => response.json())
                .then(data => console.log('Exam details created/updated successfully:', data))
                .catch(error => console.error('Error creating/updating exam details:', error));
        }


        localStorage.removeItem('qt');
        localStorage.removeItem('ExamDetails')
        navigate('/Dashboard');
        // navigate('/Dashboard', { state: { score: score } });
    };

    return (
        <div class='Result-div'>
            <div className='result-card'>
                {/* <h2>Quiz Result</h2> */}
                {renderMessage(score)}

                <Button variant='info' onClick={handleExit}>Exit</Button>

            </div>
        </div >
    );
};

export default ResultCard;


